"use strict"

window.APP_CONFIG    = require("./config.js");

var Portal           = require("./portal.js");
var Renderer         = require("./ui/renderer.js");
var Router           = require("./ui/router.js");
var LoginDialog      = require("./ui/users/login.js");
var AccountPage      = require("./ui/users/account.js");
var UserRegisterDialog= require("./ui/users/register.js");

//create route for the applications
//create the portal and started it
var webportal = new Portal(APP_CONFIG.__SERVER);
var router = new Router(webportal);
router.add("/register",  "register", UserRegisterDialog);
router.add("/account",       "account",AccountPage);
router.add("/login",    "login",   LoginDialog);


webportal.setRouter(router);
webportal.setRenderer(new Renderer(document.createElement("div")));
webportal.start();



